import { maxTempColorSecondary, minTempColorSecondary } from '../theme';
import { isNil } from 'lodash';
var newLegendClickHandler = function (e, legendItem, legend) {
    var index = legendItem.datasetIndex;
    var ci = legend.chart;
    if (isNil(index))
        return;
    // If clicked on a maximum/minimum/medium, show values for both years
    // (e.g. if clicked on maximum on first year, also show maximum for second year and hide all others)
    if (!isNil(ci.legend) && !isNil(ci.legend.legendItems)) {
        var clickedLegendText = ci.legend.legendItems[index].text.split(' ')[0];
        for (var i = 0; i < ci.legend.legendItems.length; i++) {
            var curLegendText = ci.legend.legendItems[i].text.split(' ')[0];
            if (clickedLegendText === curLegendText) {
                ci.show(i);
            }
            else {
                ci.hide(i);
            }
        }
    }
    // DEFAULT BEHAVIOR
    // if (ci.isDatasetVisible(index)) {
    // 	ci.hide(index)
    // 	legendItem.hidden = true
    // } else {
    // 	ci.show(index)
    // 	legendItem.hidden = false
    // }
};
