import { hexToRGBA } from '../utils';
import { maxTempColor, minTempColor } from '../theme';
import { isNil } from 'lodash';
export var getChartData = function (source) {
    if (isNil(source) || source.length === 0)
        return {
            labels: [],
            datasets: [],
        };
    var labels = source.slice(0, source.length - 1).map(function (x) { return x.year.toString(); });
    var dataMin = source.map(function (x) { return x.min; });
    var dataMax = source.map(function (x) { return x.max; });
    var datasets = [
        {
            label: "Temprature minimum de l'anne",
            data: dataMin,
            borderColor: minTempColor,
            backgroundColor: hexToRGBA(minTempColor, 0.5),
            borderWidth: 2,
            // Ne fonctionne pas,  voir (calculer soi mme ?)
            trendlineLinear: {
                style: 'rgba(255,105,180, .8)',
                lineStyle: 'dotted|solid',
                width: 2,
            },
        },
        {
            label: "Temprature maximum de l'anne",
            data: dataMax,
            borderColor: maxTempColor,
            backgroundColor: hexToRGBA(maxTempColor, 0.5),
            borderWidth: 2,
            trendlineLinear: {
                style: 'rgba(255,105,180, .8)',
                lineStyle: 'dotted|solid',
                width: 2,
            },
        },
    ];
    return {
        labels: labels,
        datasets: datasets,
    };
};
