import { hexToRGBA } from '../utils';
import { maxTempColor, mediumTempColor, minTempColor } from '../theme';
import { isNil } from 'lodash';
export var getChartData = function (source) {
    if (isNil(source) || source.length === 0)
        return {
            labels: [],
            datasets: [],
        };
    var labels = source.slice(0, source.length - 1).map(function (x) { return x.year.toString(); });
    var dataMin = source.map(function (x) { return x.averageOfMin; });
    var dataMax = source.map(function (x) { return x.averageOfMax; });
    var data = source.map(function (x) { return x.averageOfAverage; });
    var datasets = [
        {
            label: 'Moyenne des tempratures minimums',
            data: dataMin,
            borderColor: minTempColor,
            backgroundColor: hexToRGBA(minTempColor, 0.5),
            borderWidth: 2,
        },
        {
            label: 'Moyenne des tempratures maximums',
            data: dataMax,
            borderColor: maxTempColor,
            backgroundColor: hexToRGBA(maxTempColor, 0.5),
            borderWidth: 2,
        },
        {
            label: 'Moyenne des tempratures moyennes',
            data: data,
            borderColor: mediumTempColor,
            backgroundColor: hexToRGBA(mediumTempColor, 0.5),
            borderWidth: 2,
        },
    ];
    return {
        labels: labels,
        datasets: datasets,
    };
};
